<template>
  <v-container>
    <v-row justify="center">
      <v-card class="pa-4" max-width="600">
        <v-card-title>{{ this.userSettings['name'].value }} 様の登録情報</v-card-title>
        <v-divider thickness="2"></v-divider>
        <v-table fixed-header>
          <tbody>
            <tr v-for="(item, key) in userSettings">
              <td class="list-title">{{ item.title }}</td>
              <td class="list-value">{{ item.value }}</td>
              <td></td>
              <td v-if="item.editable">
                <v-btn icon="mdi-pencil-outline" color="blue" size="x-small" @click="beginEdit(key)"></v-btn>
              </td>
            </tr>
          </tbody>
        </v-table>
      </v-card>
    </v-row>
    <v-row>
      <v-col>
        <v-btn prepend-icon="mdi-arrow-left-circle-outline" @click="$emit('switch-view', 'clientInfo')">ユーザページTOPへ</v-btn>
      </v-col>
    </v-row>

    <!-- 登録情報再設定ダイアログ -->
    <v-dialog v-model="showEditDialog" max-width="500">
      <v-card>
        <v-card-title>{{ editInfo.title }}を再設定します</v-card-title>
        <v-card-text>
          <v-row>
            <v-col cols="4">
              <v-list-subheader>現在の設定:</v-list-subheader>
            </v-col>
            <v-col cols="8" justify="center">
              <p>{{ editInfo.beforeValue }}</p>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="4">
              <v-list-subheader>再設定値:</v-list-subheader>
            </v-col>
            <v-col cols="8">
              <v-text-field
                v-model="editInfo.afterValue"
                density="compact"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showEditDialog = false">閉じる</v-btn>
          <v-btn
            class="edit-button"
            text="設定する"
            append-icon="mdi-account-edit-outline"
            @click="registerSetting"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 編集完了ダイアログ -->
    <v-dialog v-model="showCompleteDialog" max-width="400">
      <v-card prepend-icon="mdi-check-circle-outline" title="変更完了">
        <v-card-text>
          変更が完了しました。
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showCompleteDialog = false">閉じる</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  name: "AccountInfo",
  props: {
    currentView: {
      type: String,
      required: true,
    },
  },
  created() {
    // TODO : バックエンドからユーザ設定を取得する
    this.userSettings['id'].value = 'h00001';
    this.userSettings['name'].value = '山田 太郎';
    this.userSettings['nameKana'].value = 'ヤマダ タロウ';
    this.userSettings['mail'].value = 'taro_yamada@xxx.test';
    this.userSettings['phone'].value = '090-1111-1111';
    this.userSettings['pass'].value = '＊＊＊＊＊＊＊＊';
  },
  data() {
    return {
      userSettings: {
        id        : { title: 'ID', value: '', editable: false},
        name      : { title: '氏名', value: '', editable: false},
        nameKana  : { title: '氏名(カナ)', value: '', editable: false},
        mail      : { title: 'メールアドレス', value: '', editable: true},
        phone     : { title: '電話番号', value: '', editable: true},
        pass      : { title: 'パスワード', value: '', editable: true},
      },
      showEditDialog: false,
      editInfo: {
        key: '',
        title: '',
        beforeValue: '',
        afterValue: ''
      },
      showConfirmDialog: false,
      showCompleteDialog: false,
    }
  },
  methods: {
    beginEdit(key) {
      this.editInfo.key = key;
      this.editInfo.title = this.userSettings[key].title;
      this.editInfo.beforeValue = this.userSettings[key].value;
      this.editInfo.afterValue = '';

      this.showEditDialog = true;
    },
    registerSetting() {
      this.showEditDialog = false;
      this.showCompleteDialog = true;
    },
  },
};
</script>

<style scoped>
.list-title {
  text-align: right;
}
.list-value {
  text-align: left;
}
.edit-button {
  align-self: center;
  align-items: center;
  justify-content: center;
  color: white;
  background-color: #339966;
}
</style>
